"use client";

import { motion, useScroll, useTransform } from "motion/react";
import Image from "next/image";
import Link from "next/link";
import { useRef } from "react";
import { Project } from "@/data/projects";

export default function Work({ projects }: { projects: Project[] }) {
const containerRef = useRef<HTMLDivElement>(null);
const featuredProjects = projects.slice(0, 6);

return (
<section className="relative z-20 px-4 lg:px-8 py-24 bg-white">
<div className="mx-auto">
{/* Mobile line */}



    {/* Header */}
    <div className="flex justify-between items-end mb-16 uppercase">
      <motion.h2 
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="text-[clamp(60px,9vw,150px)] font-medium leading-none"
      >
        Featured <br className="hidden md:block"/> 
        <span className="md:ml-74">Works</span>
      </motion.h2>
      <motion.span
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8, delay: 0.1 }}
        className="text-[clamp(60px,9vw,150px)] font-medium leading-none"
      >
        (06)
      </motion.span>
    </div>

    {/* Projects Grid */}
    <div ref={containerRef} className=" mx-auto">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-12 md:gap-20">
        {featuredProjects.map((project, index) => {
          const cardRef = useRef(null);
          const { scrollYProgress } = useScroll({
            target: cardRef,
            offset: ["end start", "start end"]
          });
          const scale = useTransform(scrollYProgress, [0, 0.5, 1], [1.1, 1, 1.1]);

          return (
            <motion.div
              key={index}
              ref={cardRef}
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: index * 0.1 }}
              className={`flex flex-col gap-4 ${
                index % 2 === 0 
                  ? 'items-start mr-8 md:mr-0' 
                  : 'items-end ml-8 md:ml-0'
              }`}
            >
              {/* Image container */}
              <div
                className={`relative overflow-hidden rounded-lg w-full md:max-w-[850px] ${
                  index % 2 === 0 ? 'aspect-[9/16] md:aspect-[4/5]' : 'aspect-[16/9] md:aspect-[5/4]'
                }`}
              >
                <motion.div
                  style={{ scale }}
                  className="w-full h-full"
                >
                  <Image
                    src={project.backgroundImageUrl}
                    alt={project.title}
                    fill
                    className="object-cover"
                  />
                  <div className="absolute inset-0 bg-black/20 opacity-0 hover:opacity-100 transition-opacity duration-300" />
                </motion.div>
              </div>
              
              {/* Project info */}
              <div className={`flex justify-between items-center w-full md:max-w-[550px] ${
                index % 2 === 0 ? '' : 'flex-row-reverse'
              }`}>
                <div className="flex items-center gap-2">
                  <span className="text-xl font-medium text-black">
                    ({String(index + 1).padStart(2, '0')})
                  </span>
                  <h3 className="text-xl font-medium">{project.title}</h3>
                </div>
                
                <motion.div
                  whileHover="hover"
                  className="relative overflow-hidden h-6"
                >
                  <motion.span
                    variants={{
                      hover: { y: -24 }
                    }}
                    transition={{ duration: 0.3, ease: "easeInOut" }}
                    className="block text-xl font-medium text-black cursor-pointer"
                  >
                    {project.year}
                    <span className="absolute top-6 left-0 text-black">
                      View →
                    </span>
                  </motion.span>
                </motion.div>
              </div>
            </motion.div>
          );
        })}
      </div>
    </div>

    {/* View All Button */}
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8, delay: 0.7 }}
      className="text-center mt-16"
    >
      <Link 
        href="/works"
        className="inline-block bg-black text-white px-8 py-4 rounded-full font-bold text-sm uppercase tracking-wider hover:bg-neutral-800 transition-colors duration-300"
      >
        View all works
      </Link>
    </motion.div>
  </div>
</section>


);
}